<template>
	<div class="u-accordion-container">
		<el-collapse v-model="actives">
			<slot></slot>
		</el-collapse>
	</div>
</template>

<script>
export default {
  data() {
    return {
      actives: ["1"]
    }
  }
}
</script>
<style lang="scss">
.u-accordion-container {
  .el-collapse {
    border: 0;
  }
}
</style>


