<template>
  <div class="u-title-label">
    <div class="title">
      <slot name="left">
        {{title}}
      </slot>
    </div>
    <div class="u-title-label__right-slot" v-if="hasRightSlot">
      <slot name="right"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "UTitleLabel",
  props: {
    title: {
      type: String
    }
  },
  computed: {
    hasRightSlot() {
      return this.$slots["right"]
    }
  }
}
</script>

<style lang="scss" scoped>
.u-title-label {
  display: flex;
  align-items: center;

  .title {
    display: inline-block;
    flex-grow: 1;
  }

  & &__right-slot {
    flex-grow: 0;
    align-self: start;
  }
}
</style>



