<template>
	<div class="l-section">
		<u-accordion>
			<u-accordion-item>
				<template slot="title">
					<div class="l-section__header">
						<div class="l-section__title-container">
							<div class="l-section__title">
								{{title}}
							</div>

							<div class="l-section__subtitle">
								{{subtitle}}
							</div>
						</div>
						<div class="l-section__actions">
							<slot name="right-actions">
							</slot>
						</div>
					</div>
				</template>
				<div class="l-section__content">
					<slot></slot>
				</div>
			</u-accordion-item>
		</u-accordion>
	</div>
</template>

<script>
import UAccordion from "../accordion/UAccordion"
import UAccordionItem from "../accordion/UAccordionItem"
export default {
  components: {
    UAccordion,
    UAccordionItem
  },
  props: {
    title: {
      type: String
    },
    subtitle: {
      type: String
    }
  }
}
</script>


