import { Bar, mixins } from "vue-chartjs"
const { reactiveProp } = mixins
import CustomLegendPlugin from "../CustomLegendPlugin"

export default {
  name: "BarChartWrapper",
  extends: Bar,
  mixins: [reactiveProp],
  props: ["showLegend"],
  mounted() {
    Chart.plugins.register(CustomLegendPlugin)
    this.renderChart(this.chartData, this.options)
  },
  data() {
    return {
      options: {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          legend: false
        },
        legend: {
          display: this.showLegend,
          position: "bottom",
          labels: {
            onePerLine: true,
            fontFamily: "'Roboto'",
            fontSize: 13,
            boxWidth: 30
          }
        },
        scales: {
          xAxes: [
            {
              maxBarThickness: 50,
              mirror: true,
              ticks: {
                autoSkip: false,
                maxRotation: 90
              }
            }
          ],
          yAxes: [
            {
              maxBarThickness: 50,
              offset: false,
              gridLines: {
                display: true,
                drawBorder: false
              },
              ticks: {
                min: 0
              }
            }
          ]
        },
        tooltips: {
          enabled: true,
          mode: "point",
          callbacks: {
            label: function(itens, data) {
              return `${data.datasets[itens.datasetIndex].tooltip}: ${itens.yLabel}`
            }
          }
        }
      }
    }
  }
}



