<template>
	<div class="full-stacked-bar-chart-container">
		<full-stacked-bar-chart-wrapper class="full-stacked-bar-chart" :chart-data="chartData" :showLegend="chart.chartData.showLegend" v-if="!noDataAvailable"></full-stacked-bar-chart-wrapper>
		<no-data :show="noDataAvailable" :text="noDataLabel"></no-data>
		<u-pagination :page-size="defaultPageSize" :total="barLabel.length" @change="onPageChange"></u-pagination>
	</div>
</template>

<script>
import FullStackedBarChartWrapper from "./FullStackedBarChartWrapper"
import UPagination from "../../visual/components/pagination/UPagination"
import ChartHelper from "../../../helpers/ChartHelper"
import NoData from "../NoData"

export default {
  name: "FullStackedBarChart",
  components: {
    FullStackedBarChartWrapper,
    UPagination,
    NoData
  },
  props: {
    chart: {},
    locale: {},
    colors: {},
    noDataText: {
      default: "chart.no.data"
    }
  },
  data() {
    return {
      chartData: {},
      defaultPageSize: 5,
      pagination: {}
    }
  },
  watch: {
    chart() {
      this.updateData()
    }
  },
  mounted() {
    this.updateData()
  },
  methods: {
    updateData() {
      if (this.chart.data) this.chart.data.sort((a, b) => a.group_1.localeCompare(b.group_1))
      this.chartData = this.generateData()
    },
    generateData() {
      return {
        labels: this.pagedBarLabed,
        datasets: this.pagedBarValues
      }
    },
    onPageChange: function(page) {
      this.pagination = page
      this.updateData()
    }
  },
  computed: {
    noDataLabel() {
      return this.locale[this.noDataText]
    },
    noDataAvailable() {
      return ChartHelper.isEmptyChartData(this.chart)
    },
    barLabel: function() {
      if (this.chart && this.chart.data) {
        return this.chart.data.map(it => {
          let str = this.locale.translate(it.group_1)
          if (str.length > 30) str = str.substring(0, 30) + "..."
          return str
        })
      }
      return []
    },
    pagedBarLabed: function() {
      return this.barLabel.slice(this.pagination.start, this.pagination.end)
    },
    pagedData: function() {
      if (!this.chart.data) return []
      return this.chart.data.slice(this.pagination.start, this.pagination.end)
    },
    pagedBarValues: function() {
      let metricSun = this.pagedData.map(it => {
        return this.chart.metrics.map((metric, i) => it["metric_" + (i + 1)]).reduce((acc, it) => acc + it)
      })

      return this.chart.metrics.map((metric, metricIndex) => {
        return {
          label: this.locale.translate(metric.description),
          borderWidth: 1,
          backgroundColor: this.colors[metricIndex],
          data: this.pagedData.map((it, dataIndex) => {
            let mSun = metricSun[dataIndex]
            if (mSun !== 0) {
              return it["metric_" + (metricIndex + 1)] / mSun * 100
            } else {
              return 0
            }
          })
        }
      })
    }
  }
}
</script>
<style scoped>
.full-stacked-bar-chart-container {
  height: 100%;
}

.full-stacked-bar-chart {
  height: calc(100% - 30px);
}
</style>


