export default {
  isEmptyChartData(chartData) {
    let noGroups = !chartData.groups || chartData.groups.length == 0
    let noMetrics = !chartData.metrics || chartData.metrics.length == 0
    let noData = !chartData.data || chartData.data.length == 0
    let noDataOnFirstMetric = false

    if (chartData.chartData && chartData.chartData.seriesType === "pie") {
      if (!noData) {
        noDataOnFirstMetric = chartData.data.filter(m => m.metric_1 > 0).length === 0
      }
    }

    return noGroups || noMetrics || noData || noDataOnFirstMetric
  }
}



