<template>
	<el-radio-group v-model="v" :name="groupName" @change="change">
		<el-radio :label="item.value" v-for="(item, index) in options" :key="index">{{item.label}}</el-radio>
	</el-radio-group>
</template>

<script>
export default {
  props: {
    groupName: {
      type: String,
      required: true
    },
    options: {
      type: Array,
      required: true
    },
    value: {}
  },
  data() {
    return {
      v: this.value
    }
  },
  watch: {
    value() {
      this.v = this.value
    }
  },
  methods: {
    change(val) {
      this.$emit("input", val)
      this.$emit("change", val)
    }
  }
}
</script>

<style lang="scss">
.el-radio-group {
  display: flex;
  font-weight: inherit;
  font-size: inherit;
}
.el-radio {
  font-size: inherit;
  display: flex;
  font-weight: inherit;

  &__label {
    font-size: inherit;
  }
  .el-radio__input.is-checked + .el-radio__label {
    color: #555;
  }
  .el-radio__input.is-checked .el-radio__inner {
    border-color: #555;
    background: #555;
  }
}
</style>



