<template>
	<span class="c-badge">
		{{label}}
	</span>
</template>

<script>
export default {
  name: "UBadge",
  props: {
    label: {
      type: String
    }
  }
}
</script>

<style lang="scss" scoped>
.c-badge {
  padding: 4px 5px;
  margin-left: 5px;
  border: 1px solid;
  border-radius: 0.2rem;
  background-color: hsla(225, 4%, 58%, 0.1);
  border-color: hsla(225, 4%, 58%, 0.2);
  color: #333;
  white-space: nowrap;
}
</style>



