<template>
	<div class="stacked-bar-chart-container">
		<stacked-bar-chart-wrapper class="stacked-bar-chart" :chart-data="chartData" :showLegend="chart.chartData.showLegend" v-if="!noDataAvailable"></stacked-bar-chart-wrapper>
		<no-data :show="noDataAvailable" :text="noDataLabel"></no-data>
		<u-pagination :page-size="defaultPageSize" :total="barLabel.length" @change="onPageChange"></u-pagination>
	</div>
</template>

<script>
import StackedBarChartWrapper from "./StackedBarChartWrapper"
import UPagination from "../../visual/components/pagination/UPagination"
import ChartHelper from "../../../helpers/ChartHelper"
import NoData from "../NoData"

export default {
  name: "StackedBarChart",
  components: {
    StackedBarChartWrapper,
    UPagination,
    NoData
  },
  props: {
    chart: {},
    locale: {},
    colors: {},
    noDataText: {
      default: "chart.no.data"
    }
  },
  data() {
    return {
      chartData: {},
      defaultPageSize: 5,
      pagination: {}
    }
  },
  watch: {
    chart() {
      this.updateData()
    }
  },
  mounted() {
    this.updateData()
  },
  methods: {
    updateData() {
      if (this.chart.data) this.chart.data.sort((a, b) => a.group_1.localeCompare(b.group_1))
      this.chartData = this.generateData()
    },
    generateData() {
      return {
        labels: this.pagedBarLabed,
        datasets: this.pagedBarValues
      }
    },
    onPageChange: function(page) {
      this.pagination = page
      this.updateData()
    }
  },
  computed: {
    noDataLabel() {
      return this.locale[this.noDataText]
    },
    noDataAvailable() {
      return ChartHelper.isEmptyChartData(this.chart)
    },
    barLabel: function() {
      if (this.chart && this.chart.data) {
        return this.chart.data.map(it => {
          let str = this.locale.translate(it.group_1)
          if (str.length > 30) str = str.substring(0, 30) + "..."
          return str
        })
      }
      return []
    },
    pagedBarLabed: function() {
      return this.barLabel.slice(this.pagination.start, this.pagination.end)
    },
    pagedBarValues: function() {
      if (this.chart && this.chart.metrics) {
        return this.chart.metrics.map((it, i) => {
          let str = this.locale.translate(it.description)
          return {
            label: str,
            borderWidth: 1,
            backgroundColor: this.colors[i],
            data: this.chart.data
              ? this.chart.data.map(it => it["metric_" + (i + 1)]).slice(this.pagination.start, this.pagination.end)
              : []
          }
        })
      }
      return []
    }
  }
}
</script>
<style scoped>
.stacked-bar-chart-container {
  height: 100%;
}

.stacked-bar-chart {
  height: calc(100% - 30px);
}
</style>


