<template>
	<div class="pie-chart-container">
		<pie-chart-wrapper class="pie-chart" :chart-data="chartData" v-if="!noDataAvailable"></pie-chart-wrapper>
		<no-data :show="noDataAvailable" :text="noDataLabel"></no-data>
	</div>
</template>

<script>
import PieChartWrapper from "./PieChartWrapper"
import ChartHelper from "../../../helpers/ChartHelper"
import NoData from "../NoData"

export default {
  components: {
    PieChartWrapper,
    NoData
  },
  props: {
    chart: {},
    locale: {},
    colors: {},
    noDataText: {
      default: "chart.no.data"
    }
  },
  data() {
    return {
      chartData: {}
    }
  },
  watch: {
    chart() {
      this.chartData = this.fillData()
    }
  },
  mounted() {
    this.chartData = this.fillData()
  },
  methods: {
    fillData() {
      return {
        labels: this.pieLabel,
        datasets: [
          {
            backgroundColor: this.colors,
            borderWidth: 1,
            data: this.pieData
          }
        ]
      }
    }
  },
  computed: {
    noDataLabel() {
      return this.locale[this.noDataText]
    },
    noDataAvailable() {
      return ChartHelper.isEmptyChartData(this.chart)
    },
    pieLabel: function() {
      if (this.chart && this.chart.data) {
        return this.chart.data.map(it => {
          let str = this.locale.translate(it.group_1)
          if (str.length > 30) str = str.substring(0, 30) + "..."
          return str
        })
      }
      return ""
    },
    pieData: function() {
      if (this.chart && this.chart.data) {
        return this.chart.data.map(it => {
          return it.metric_1
        })
      }
      return [{}]
    }
  }
}
</script>
<style scoped>
.pie-chart-container,
.pie-chart {
  height: 100%;
}
</style>


