<template>
	<line-chart-wrapper class="line-chart" :chart-data="chartData"></line-chart-wrapper>
</template>

<script>
import LineChartWrapper from "./LineChartWrapper"

export default {
  name: "LineChart",
  components: {
    LineChartWrapper
  },
  props: ["chart", "locale", "colors"],
  data() {
    return {
      chartData: {}
    }
  },
  watch: {
    chart() {
      this.updateData()
    }
  },
  mounted() {
    this.updateData()
  },
  methods: {
    updateData() {
      this.chartData = this.fillData()
    },
    fillData() {
      return {
        labels: this.chart.data.map(it => {
          return this.locale.translate(it.group_1)
        }),
        datasets: [
          {
            label: "",
            pointStyle: "circle",
            pointRadius: 5,
            pointBackgroundColor: "Black",

            pointHoverRadius: 6,
            pointHoverBorderWidth: 4,
            pointHoverBackgroundColor: "White",
            pointHoverBorderColor: "Black",

            fill: false,
            seriesName: this.chart.metrics.map(it => this.locale.translate(it.description)),
            data: this.chart.data.map(it => {
              return { x: this.locale.translate(it.group_1), y: it.metric_1 }
            })
          }
        ]
      }
    }
  }
}
</script>
<style scoped>
.line-chart {
  height: 100%;
}
</style>



