<template>
	<div class="image-profile">
		<img :src="image" class="agent-image" v-if="shouldRenderImage" />
		<div class="agent-image" v-if="!shouldRenderImage">
			<i class="fas fa-user-circle"></i>
		</div>
	</div>
</template>

<script>
export default {
  name: "AgentProfileImage",
  props: ["image"],
  computed: {
    shouldRenderImage: function() {
      return this.image !== ""
    }
  }
}
</script>

<style lang="scss" scoped>
.image-profile {
  position: relative;

  .agent-image {
    position: absolute;
    top: 0px;
    background-repeat: no-repeat;
    background-position: center;
    height: 45px;
    width: 45px;
    display: inline-block;
    -webkit-background-size: contain;
    -moz-background-size: contain;
    background-size: contain;
    border-radius: 50%;
    font-size: 45px;
  }
}
</style>



