<template>
  <div class="c-map-container">
    <gmap-map :center="effectiveCenter" :zoom="zoom" map-type-id="roadmap" class="c-map-container__full" ref="map">
      <gmap-marker v-for="(marker, index) in markers" :key="index" :icon="markerColor(marker)" :position="marker.position" @click="openInfoWindow(marker)" />
      <gmap-info-window :position="selectedMarker.position" :opened="infoWindowOpen" @closeclick="closeInfoWindow">
        <slot name="info-window">
        </slot>
      </gmap-info-window>
      <gmap-polyline v-for="(polyline, index) in polylines" :key="index" :path="polyline.positions" :options="polylineOptions(polyline)" />
      <gmap-polyline v-for="(stepPolyline, index) in getPolylinesFromRoutes()" :key="index" :path="stepPolyline.path" :options="stepPolyline" />
      <div slot="visible">
        <slot></slot>
      </div>
    </gmap-map>
  </div>
</template>

<script>
import * as VueGoogleMaps from "vue2-google-maps"

const DEFAULT_CENTER = {
  lat: -17.1456882,
  lng: -49.7798804
}

export default {
  name: "UMap",
  props: {
    polylines: {
      type: Array
    },
    markers: {
      type: Array
    },
    routes: {
      type: Array
    },
    gmapsLoaded: {
      type: Boolean,
      default: false
    },
    center: {
      type: Object,
      default: () => {
        return DEFAULT_CENTER
      }
    },
    zoom: {
      type: Number,
      default: 4
    }
  },
  mounted() {
    VueGoogleMaps.loaded.then(() => {
      this.isGmapsLoaded = true
      this.centerMapOnMarkers()
    })
  },
  data() {
    return {
      isGmapsLoaded: this.gmapsLoaded,
      selectedMarker: {},
      infoWindowOpen: false,
      defaultStroke: {
        strokeColor: "#000000",
        strokeWeight: 1
      },
      mapBounds: null,
      effectiveCenter: this.center
    }
  },
  watch: {
    center() {
      this.effectiveCenter = this.center
    },
    markers() {
      this.centerMapOnMarkers()
    },
    gmapsLoaded() {
      this.isGmapsLoaded = this.gmapsLoaded
      this.centerMapOnMarkers()
    }
  },
  methods: {
    centerMapOnMarkers() {
      if (!this.isGmapsLoaded) return

      var bounds = new google.maps.LatLngBounds()

      if (this.markers && this.markers.length > 0) {
        this.markers.forEach(marker => {
          bounds.extend(new google.maps.LatLng(marker.position.lat, marker.position.lng))
        })
        this.$refs.map.fitBounds(bounds)
      } else {
        this.effectiveCenter = DEFAULT_CENTER
      }
    },
    openInfoWindow(marker) {
      if (!marker.infoWindow) return

      this.infoWindowOpen = true
      this.selectedMarker = marker
      this.$emit("selectedMarkerChange", this.selectedMarker)
    },
    closeInfoWindow() {
      this.infoWindowOpen = false
    },
    markerColor(marker) {
      let colorHexa
      if (!marker.color) {
        colorHexa = "FE7569"
      } else {
        colorHexa = marker.color
      }
      return `http://chart.apis.google.com/chart?chst=d_map_pin_letter&chld=%E2%80%A2|${colorHexa}`
    },
    polylineOptions(polyline) {
      let options = Object.assign({}, this.defaultStroke)
      if (polyline.weight) {
        options.strokeWeight = polyline.weight
      }
      if (polyline.color) {
        options.strokeColor = polyline.color
      }
      return options
    },
    getPolylinesFromRoutes() {
      if (this.isGmapsLoaded && this.routes) {
        let allRoutesPolylines = []
        this.routes.forEach(r => {
          let routePolylines = r.steps.forEach(step => {
            var decodedPath = window.google.maps.geometry.encoding.decodePath(step)
            var decodedLevels = this.decodeLevels("B".repeat(decodedPath.length))

            var stepPolyline = {
              path: decodedPath,
              levels: decodedLevels,
              strokeColor: r.color || this.defaultStroke.strokeColor,
              strokeWeight: r.weight || this.defaultStroke.strokeWeight
            }

            allRoutesPolylines.push(stepPolyline)
          })
        })
        return allRoutesPolylines
      }
      return []
    },
    decodeLevels(encodedLevelsString) {
      var decodedLevels = []

      for (var i = 0; i < encodedLevelsString.length; ++i) {
        var level = encodedLevelsString.charCodeAt(i) - 63
        decodedLevels.push(level)
      }
      return decodedLevels
    }
  },
  computed: {}
}
</script>

<style lang="scss" scoped>
.c-map-container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  right: 0;
  left: 0;
  & &__full {
    height: 100%;
    width: 100%;
  }
}
</style>


