<template>
	<div class="l-dropdown u-select-dropdown">
		<div class="dropdown">
			<!-- <button aria-expanded="true" class="dropdown-toggle" data-toggle="dropdown" type="button" @click="toggleDropdown">
        <ul class="dropdown-menu l-select__list list-group list-group-flush">
          
		    </ul>
      </button> -->
			<button class="dropdown-toggle" @click="toggleDropdown">
				<!-- <button class="btn btn-secondary dropdown-toggle" type="button" @click="toggleDropdown"> -->
				<span class="status">{{title}}</span>
			</button>
			<div class="dropdown-menu l-dropdown__list" :class="{'show' : open}" aria-labelledby="dropdownMenuButton">
				<u-select-dropdown-option v-for="(option, index) in options" :key="index" :option="option" @toggle="toggle"></u-select-dropdown-option>
			</div>
		</div>
	</div>
</template>

<script>
import USelectDropdownOption from "./USelectDropdownOption"

export default {
  components: {
    USelectDropdownOption
  },
  props: ["title", "options"],
  data() {
    return {
      open: false
    }
  },
  methods: {
    toggleDropdown() {
      this.open = !this.open
    },
    toggle(option) {
      this.inactiveAll()
      option.active = !option.active
      if (option.content) {
        option.content.active = option.active
      }
      this.$emit("changed", option)
    },
    inactiveAll() {
      for (let option of this.options) {
        option.active = false
        if (option.content) {
          option.content.active = false
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.u-select-dropdown {
  display: inline-block;
  width: 155px;
  outline: 0;

  .dropdown-toggle {
    border: 0;
    background: no-repeat center right;
    color: #666;
    cursor: pointer;
    display: block;

    &:focus {
      outline: 0;
    }
  }
}

.l-dropdown {
  & &__list {
    font-size: inherit;
  }
}
</style>


