<template>
	<div class="u-checkbox">
		<el-checkbox :title="hint" v-model="selectedValue" :label="label" :disabled="disabled" @change="change"></el-checkbox>
	</div>
</template>

<script>
export default {
  name: "UCheckbox",
  props: {
    label: {
      type: String
    },
    disabled: {
      type: Boolean,
      default: false
    },
    hint: {
      type: String
    },
    dataValue: {},
    value: {}
  },
  data() {
    return {
      selectedValue: this.value
    }
  },
  watch: {
    value() {
      this.selectedValue = this.value
    }
  },
  methods: {
    change(val) {
      this.$emit("input", val)
      this.$emit("change", val, this.dataValue)
    }
  }
}
</script>

<style lang="scss">
.u-checkbox {
  .el-checkbox__input.is-checked .el-checkbox__inner,
  .el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background-color: #555;
    border-color: #555;
  }

  .el-checkbox__input .el-checkbox__inner:hover {
    border-color: #555;
  }

  .el-checkbox {
    font-size: inherit;
  }

  .el-checkbox__label {
    font-weight: 400;
    font-size: inherit;
    color: #555;
  }

  .el-checkbox__input.is-checked + .el-checkbox__label {
    color: #555;
  }
}
</style>



