<template>
  <div class="l-page">
    <h1 class="page-title l-page__title">{{title}}</h1>
  </div>
</template>

<script>
export default {
  props: ["title"]
}
</script>

<style lang="scss" scoped>
.l-page {
  & &__title {
    margin-bottom: 0px;
  }

  & .page-title {
    color: #555;
    text-shadow: 1px 1px 1px #ccc;
  }

  & h1 {
    font-size: 2em;
  }
}
</style>



