<template>
	<div class="pagination-container">
		<el-pagination :page-size="pagination.size" layout="prev, pager, next" :total="total" @current-change="change">
		</el-pagination>
	</div>
</template>

<script>
export default {
  name: "UPagination",
  props: {
    total: {
      required: true
    },
    pageSize: {
      default: 10
    }
  },
  data() {
    return {
      pagination: {
        current: 1,
        size: this.pageSize
      }
    }
  },
  computed: {
    page: function() {
      return {
        start: (this.pagination.current - 1) * this.pagination.size,
        end: this.pagination.current * this.pagination.size
      }
    }
  },
  methods: {
    change(val) {
      this.pagination.current = val
      this.emitChange()
    },
    emitChange() {
      this.$emit("change", this.page)
    }
  },
  mounted() {
    this.emitChange()
  }
}
</script>

<style lang="scss">
.pagination-container {
  bottom: 0px;
  left: 0px;
  right: 0px;
  text-align: center;
}
</style>



