<template>
	<div class="no-data" v-if="show">{{noDataText}}</div>
</template>

<script>
export default {
  name: "NoData",
  props: ["show", "text"],
  computed: {
    noDataText() {
      return this.text || "Sem dados..."
    }
  }
}
</script>

<style scoped>
.no-data {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100% - 50px);
  color: #909399;
  padding: 20px 0;
  border-top: 1px solid;
  border-bottom: 1px solid;
  border-color: rgba(144, 147, 153, 0.2);
}
</style>



