<template>
	<div data-partial="label">
		<label>{{text}}</label>
	</div>
</template>

<script>
export default {
  name: "ULabel",
  props: {
    text: {
      type: String
    }
  }
}
</script>

<style>
[data-partial="label"] > label {
  color: #666;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}
</style>



