<template>
  <div class="c-anchor-container" v-on="{ click: !disabled ? click : noAction }">
    <template v-if="disabled">
      <u-icon :fa-icon="faIcon" :class="`icon-${severity}`" disabled />
    </template>
    <template v-else>
      <a :title="tooltip" :href="href">
        <slot>
          <div v-if="faIcon">
            <u-icon :fa-icon="faIcon" :class="`icon-${severity}`" />
          </div>
          <span>{{title}}</span>
        </slot>
      </a>
    </template>
  </div>
</template>

<script>
import UIcon from "../icon/UIcon"

export default {
  name: "UAnchor",
  components: {
    UIcon
  },
  props: {
    title: {},
    faIcon: {},
    tooltip: {},
    href: {},
    severity: {
      type: String,
      default: "default"
    },
    disabled: {
      type: Boolean
    }
  },
  methods: {
    click(ev) {
      this.$emit("click", ev)
    },
    noAction() {}
  }
}
</script>

<style lang="scss" scoped>
.c-anchor-container {
  margin: 0 3px;
  cursor: pointer !important;
  padding: 2px;
  display: inline-block;
}
</style>



