import { HorizontalBar, mixins } from "vue-chartjs"
const { reactiveProp } = mixins
import CustomLegendPlugin from "../CustomLegendPlugin"
import StackedBarChartPercentPlugin from "./StackedBarChartPercentPlugin"

export default {
  extends: HorizontalBar,
  name: "StackedBarWrapper",
  mixins: [reactiveProp],
  props: ["showLegend"],
  mounted() {
    Chart.plugins.register(CustomLegendPlugin)
    this.addPlugin(StackedBarChartPercentPlugin)
    this.renderChart(this.chartData, this.options)
  },
  data() {
    return {
      options: {
        responsive: true,
        maintainAspectRatio: false,
        fullStacked: false,
        plugins: {
          legend: false
        },
        legend: {
          display: this.showLegend,
          position: "bottom",
          labels: {
            onePerLine: true,
            fontFamily: "'Roboto'",
            fontSize: 13,
            boxWidth: 30
          }
        },
        tooltips: {
          enabled: true,
          mode: "point",
          callbacks: {
            label: function(itens, data) {
              let allData = data.datasets.reduce((acc, ds) => acc + ds.data[itens.index], 0)
              let representativeShare = (itens.xLabel * 100) / (allData != 0 ? allData : 1)
              return `${
                data.datasets[itens.datasetIndex].label
              }: ${itens.xLabel.toFixed()} (${representativeShare.toFixed()}%)`
            }
          }
        },
        scales: {
          xAxes: [
            {
              maxBarThickness: 30,
              stacked: true,
              gridLines: {
                display: true,
                drawBorder: false
              }
            }
          ],
          yAxes: [
            {
              maxBarThickness: 30,
              stacked: true,
              beginAtZero: true
            }
          ]
        }
      }
    }
  }
}



