<template>
  <div class="table-content">
    <div class="table-container">
      <el-table :data="page" highlight-current-row stripe height="from-container" cell-class-name="table-body" header-cell-class-name="table-header" :show-summary="showFooter" size="mini" @sort-change="customSort" @filter-change="customFilter">
        <el-table-column :class-name="columnClass" v-for="(column, index) in columns" :key="index" :prop="column.name" :label="column.label" :filters="filters(column)" :column-key="column.name" sortable="custom" />
      </el-table>
    </div>
    <u-pagination :total="displayedData.length" :pageSize="7" @change="paginationChange"></u-pagination>
  </div>
</template>

<script>
import UPagination from "../../visual/components/pagination/UPagination"
export default {
  name: "UTable",
  components: {
    UPagination
  },
  data() {
    return {
      pagination: {},
      filterInfo: {},
      sortInfo: {},
      incrementInfo: 0
    }
  },
  props: {
    table: {
      type: Object,
      required: true
    },
    showFooter: {
      type: Boolean,
      required: false,
      default: false
    },
    dataFontSize: {
      type: String,
      required: false
    }
  },
  computed: {
    columnClass() {
      if (this.dataFontSize) {
        return `table-column-font-${this.dataFontSize}`
      }
      return ""
    },
    columns: function() {
      if (this.table && this.table.columns) {
        return this.table.columns
      }
      return []
    },
    data: function() {
      if (this.table && this.table.datasource) {
        return this.table.datasource
      }
      return []
    },
    displayedData: function() {
      return this.orderData(this.filterData(this.data))
    },
    page: function() {
      return this.displayedData.slice(this.pagination.start, this.pagination.end)
    }
  },
  methods: {
    orderData: function(data) {
      let columnKey = this.columns[0].name
      let sort = function(a, b) {
        if (typeof a === "string") {
          return a.localeCompare(b)
        }
        return a - b
      }
      return data.sort((first, second) => {
        if (!this.sortInfo.order) {
          return sort(first[columnKey], second[columnKey])
        }
        if (this.sortInfo.order === "descending") return sort(first[this.sortInfo.prop], second[this.sortInfo.prop])
        else return sort(second[this.sortInfo.prop], first[this.sortInfo.prop])
      })
    },
    filterData: function(data) {
      let filterKeys = Object.keys(this.filterInfo)
      if (filterKeys.length === 0) return data
      return data.filter(it => {
        return (
          filterKeys
            .map(fk => {
              return this.filterInfo[fk].includes(it[fk])
            })
            .filter(it => it).length === filterKeys.length
        )
      })
    },
    customSort: function({ prop, order }) {
      this.sortInfo = {
        prop,
        order
      }
    },
    customFilter: function(filters, outro) {
      let columnKey = Object.keys(filters)[0]
      this.filterInfo[columnKey] = filters[columnKey]
      if (filters[columnKey].length === 0) {
        delete this.filterInfo[columnKey]
      }
      this.filterInfo = Object.assign({}, this.filterInfo)
    },
    filters: function(column) {
      if (!this.table || !this.table.datasource) return []
      let filter = this.table.datasource
        .map(it => {
          let val = it[column.name]
          return {
            text: it[column.name],
            value: it[column.name]
          }
        })
        .reduce((acc, it) => {
          let contains = acc.find(ac => ac.text === it.text)
          if (!contains) {
            acc.push(it)
          }
          return acc
        }, [])
      return filter
    },
    paginationChange(page) {
      this.pagination = page
    }
  }
}
</script>

<style lang="scss" scoped>
.table-content {
  position: absolute;
  bottom: 0px;
  left: 0px;
  right: 0px;
  top: 0px;

  .table-container {
    position: absolute;
    padding-left: 10px;
    padding-right: 10px;
    bottom: 32px;
    top: 35px;
    left: 0px;
    right: 0px;
  }

  .pagination-container {
    position: absolute;
  }
}
</style>

<style lang="scss">
.el-table-filter__content {
  overflow-y: auto;
  max-height: 300px;

  .el-checkbox__input.is-checked .el-checkbox__inner,
  .el-checkbox__input.is-indeterminate .el-checkbox__inner {
    background-color: #555;
    border-color: #555;
  }
  .el-checkbox__input.is-checked + .el-checkbox__label {
    color: #555;
  }

  .el-checkbox__label {
    font-weight: normal;
    font-size: 12px;
  }
}

.el-table {
  height: 100%;
  .table-header .cell {
    word-break: keep-all;
    display: flex;
    align-items: center;
  }

  th > .cell.highlight {
    color: #555;
  }

  .table-body .cell {
    word-break: keep-all;
  }

  .caret-wrapper .sort-caret {
    right: 3px;
    left: auto;
  }
  .descending .sort-caret.descending {
    border-top-color: #555;
  }
  .ascending .sort-caret.ascending {
    border-bottom-color: #555;
  }

  .table-column-font-normal {
    font-size: 1em;
  }

  .table-column-font-medium {
    font-size: 1.5em;
  }

  .table-column-font-big {
    font-size: 2em;
  }
}
</style>


