<template>
	<div class="c-button-container">
		<button class="btn btn-sm c-button -side-margin" :title="tooltip" @click="click" :disabled="disabled" :class="{'-is-active': active}">
			<span class="c-button__icon">
				<i :class="faicon" aria-hidden="true" v-if="faicon"></i>
			</span>
			<span>{{title}}</span>
		</button>
		<slot name="content"></slot>
	</div>
</template>

<script>
export default {
  name: "UButton",
  props: {
    title: { type: String },
    faicon: { type: String },
    active: { type: Boolean, default: false },
    tooltip: { type: String },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    click(ev) {
      this.$emit("click", ev)
    }
  }
}
</script>

<style lang="scss" scoped>
.c-button {
  &.-side-margin {
    margin: 0px 5px;
  }
  &.-is-active {
    background-color: #cac9c9;
    border-color: #00808b;
  }
}
</style>



