<template>
	<div class="agent-profile-holder">
		<div class="row">
			<div class="col-md-6">
				<div class="battery-holder">
					<i class="fas fa-battery-three-quarters"></i>
					<span>
						<template v-if="agentProfile.battery !== '-'">
							{{agentProfile.battery}}%
						</template>
						<template v-if="agentProfile.battery === '-'">
							{{agentProfile.battery}}
						</template>
					</span>
					<div class="platform-type-holder">
						<i :class="platformTypeImageClass"></i>
					</div>
				</div>
			</div>
			<div class="col-md-6 right">
				{{agentProfile.lastsync_complete}}
			</div>
		</div>

		<div class="row">
			<div class="col-12">
				<div class="agent-holder">
					<agent-profile-image :image="pictureurl"></agent-profile-image>
					<div class="user-info">
						<p class="name">{{agentName}}</p>
						<p class="task">
							<i class="fas fa-check"></i>
							<span class="uprogress">
								{{percent}}%
							</span>
							{{ locale['dashboard.widget.agent.finished-tasks']}}
						</p>
					</div>
				</div>
			</div>
		</div>

		<div class="row">
			<div class="col-12">
				<div class="data-holder">
					<div class="row">
						<div class="col-6">
							<div class="field">
								<span class="value">{{agentProfile.returnedtasks}}/{{agentProfile.alltasks}}</span>
								<span class="label">{{locale['tasks']}}</span>
							</div>
						</div>
						<div class="col-6">
							<div class="field">
								<span class="value">{{agentProfile.executedactivities}}/{{agentProfile.allactivities}}</span>
								<span class="label">{{locale['activities']}}</span>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import AgentProfileImage from "./AgentProfileImage"

export default {
  components: {
    AgentProfileImage
  },
  props: ["agentProfile", "locale"],
  computed: {
    pictureurl: function() {
      if (this.agentProfile && this.agentProfile.pictureurl) {
        return this.agentProfile.pictureurl
      }
      return ""
    },
    platformTypeImageClass: function() {
      if (this.agentProfile.mobileplatformtype === "ANDROID") return "fab fa-android"
      if (this.agentProfile.mobileplatformtype === "IOS") return "fab fa-apple"
      return ""
    },
    agentName: function() {
      if (this.agentProfile && this.agentProfile.age_name) {
        return this.agentProfile.age_name
      }
      return ""
    },
    percent: function() {
      let part = this.agentProfile.returnedtasks
      let total = this.agentProfile.alltasks

      if (total === 0) {
        return parseFloat(0).toFixed(0)
      }

      var percentage = part * 100 / total
      if (percentage > 100) {
        percentage = 100
      }

      if (percentage === 100) {
        return 100
      } else {
        return parseFloat(percentage).toFixed(0)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.agent-profile-holder {
  padding: 7px;
  margin-bottom: 25px;
  color: #fff;
  font: 11px "Roboto", sans-serif;
  font-weight: 600;
  background-color: #3f51b5;

  .offline & {
    background-color: #9fa8da;
  }

  .battery-holder {
    display: inline-flex;
    position: relative;
    font-size: 14px;
    left: 5px;
    margin-bottom: 10px;

    span {
      font-size: 10px;
      font-weight: initial;
      padding-left: 3px;
    }
  }

  .platform-type-holder {
    display: inline-flex;
    margin-left: 5px;
    font-size: 14px;
  }

  .agent-holder {
    position: relative;
    padding: 0px;
    height: 50px;

    .user-info {
      position: relative;
      margin-left: 5px;
      left: 55px;

      .name {
        position: relative;
        left: 1px;
        font-size: 13px;
        font-weight: 700;
        margin: 0;
      }
      .task {
        margin: 0;

        .uprogress {
          font: 14px "Roboto", sans-serif;
          font-weight: 600;
          position: relative;
          color: #0ebc71;
          .offline & {
            color: #e45851;
          }
        }
      }
      .resume {
        margin-left: 10px;
      }
    }
  }

  .data-holder .field {
    text-align: center;

    .value {
      font-size: 20px;
      font-weight: 600;
      display: block;
    }
    .label {
      line-height: 18px;
    }
  }
}

.right {
  text-align: right;
}
</style>



