let font = {
  size: 10,
  style: "normal",
  family: "Roboto",
  align: "center",
  baseline: "middle",
  fillStyle: "#242424"
}

export default {
  id: "pie-chart-percent-plugin",
  afterDatasetsDraw: function(chart) {
    let ctx = chart.ctx
    ctx.fillStyle = font.fillStyle
    ctx.textAlign = font.align
    ctx.textBaseline = font.baseline
    ctx.font = Chart.helpers.fontString(font.size, font.style, font.family)

    chart.data.datasets.forEach((dataset, index) => {
      let allData = dataset.data.reduce((acc, ds) => acc + ds, 0)
      chart.getDatasetMeta(index).data.forEach((element, index) => {
        let data = dataset.data[index]
        if (!element.hidden && data > 0) {
          let position = element.getCenterPoint()
          ctx.save()
          ctx.translate(position.x, position.y)
          let representativeShare = (data * 100) / (allData != 0 ? allData : 1)
          ctx.fillText(`${representativeShare.toFixed()}%`, 0, 0)
          ctx.restore()
        }
      })
    })
  }
}



