<template>
	<div class="l-section__subsection">
		<u-accordion>
			<u-accordion-item>
				<template slot="title">
					<div class="l-section__subsection-header">
						{{title}}
					</div>
				</template>
				<div class="l-section__subsection-content">
					<slot></slot>
				</div>
			</u-accordion-item>
		</u-accordion>
	</div>
</template>

<script>
import UAccordion from "../accordion/UAccordion"
import UAccordionItem from "../accordion/UAccordionItem"
export default {
  components: {
    UAccordion,
    UAccordionItem
  },
  props: {
    title: {
      type: String
    },
    subtitle: {
      type: String
    }
  }
}
</script>

<style lang="scss" scoped>
.l-section__subsection {
  .l-section__subsection-header {
    width: 100%;
    font-size: 1.2em;
    color: #555;
  }
}
</style>

<style lang="scss">
.l-section__subsection {
  .c-collapse-icon__container {
    margin-right: 5px;
    font-size: 1.2em !important;
  }
}
</style>



