let font = {
  size: 10,
  style: "normal",
  family: "Roboto",
  align: "center",
  baseline: "middle",
  fillStyle: "#242424"
}

export default {
  id: "full-stacked-bar-chart-percent-plugin",
  afterDatasetsDraw: function(chart) {
    let ctx = chart.ctx
    ctx.fillStyle = font.fillStyle
    ctx.textAlign = font.align
    ctx.textBaseline = font.baseline
    ctx.font = Chart.helpers.fontString(font.size, font.style, font.family)

    chart.data.datasets.forEach(function(dataset, i) {
      var meta = chart.getDatasetMeta(i)
      if (!meta.hidden) {
        meta.data.forEach(function(element, index) {
          let data = dataset.data[index]
          if (data > 0) {
            ctx.save()
            var position = element.getCenterPoint()
            ctx.translate(position.x, position.y)
            ctx.rotate(Math.PI / 3)
            ctx.fillText(data.toFixed().toString() + "%", 0, 0)
            ctx.restore()
          }
        })
      }
    })
  }
}



