<template>
  <div class="gauge-widget">
    <u-title-label>
      <template slot="left">
        <span>{{title}}</span>
      </template>
      <template slot="right">
        <u-badge :label="period"></u-badge>
      </template>
    </u-title-label>
    <div class="gauge-container">
      <div class="gauge-value">
        {{gauge.cardData.maxValue}}
        <span class="gauge-value-separator">/</span>{{gauge.cardData.minValue}}
      </div>
      <div class="gauge-bow">
        <canvas ref="gauge" class="gauge-canvas"></canvas>
        <div class="gauge-percent">
          {{gauge.cardData.value}}%
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UTitleLabel from "../../visual/layout/label/UTitleLabel"
import UBadge from "../../visual/components/badge/UBadge"

export default {
  components: {
    UTitleLabel,
    UBadge
  },
  props: ["gauge", "locale", "properties"],
  data() {
    return {
      gaugeComponent: null
    }
  },
  mounted() {
    var opts = {
      angle: 0,
      lineWidth: 0.49,
      radiusScale: 1,
      pointer: {
        strokeWidth: 0
      },
      staticLabels: {
        font: "10px sans-serif",
        labels: [0, 25, 50, 75, 100],
        color: "#000000",
        fractionDigits: 0
      },
      limitMax: true,
      limitMin: true,
      colorStart: "#283593",
      colorStop: "#283593",
      strokeColor: "#E0E0E0",
      generateGradient: true,
      highDpiSupport: true
    }
    this.gaugeComponent = new window.Gauge(this.$refs.gauge).setOptions(opts)
    this.gaugeComponent.maxValue = 100
    this.gaugeComponent.setMinValue(0)
    this.gaugeComponent.animationSpeed = 10
    this.gaugeComponent.fontSize = 50
    this.gaugeComponent.set(this.gauge.cardData.value)
  },

  watch: {
    gauge() {
      this.gaugeComponent.set(this.gauge.cardData.value)
    }
  },
  computed: {
    title() {
      if (this.properties && this.properties.title) {
        return this.properties.title
      }
      return this.locale[this.gauge.cardData.title]
    },
    period: function() {
      if (this.gauge && this.gauge.cardData) {
        return this.locale[this.gauge.cardData.subtitle]
      }
      return ""
    }
  }
}
</script>

<style lang="scss" scoped>
.gauge-widget {
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  .gauge-container {
    display: flex;
    flex-direction: column;
    flex-grow: 1;

    .gauge-value {
      font: 32px "Roboto", sans-serif;
      font-weight: 500;
      line-height: 0.9;
      margin-top: 10px;

      .gauge-value-separator {
        font-size: 30px;
      }
    }

    .gauge-bow {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      .gauge-canvas {
        width: 100%;
      }

      .gauge-percent {
        margin-top: -45px;
        text-align: center;
        font-weight: bold;
        color: #4c4c4c;
        font: 30px "Roboto", sans-serif;
      }
    }
  }
}
</style>



