<template>
	<div class="agent-profile-container">
		<div class="row">
			<div class="col-12">
				<agent-management-header :title="title" :locale="locale" @filterchange="onFilterChange" @sortchange="onSortChange"></agent-management-header>
			</div>
		</div>
		<div class="row">
			<div class="col-12">
				<div class="row">
					<div class="col-12 col-sm-6 col-md-4 col-lg-3 col-xl-3" v-for="agentProfile in pagedData" :key="agentProfile.id">
						<agent-profile :agentProfile="agentProfile" :locale="locale"></agent-profile>
					</div>
				</div>
			</div>
		</div>
		<u-pagination :page-size="defaultPageSize" :total="filteredData.length" @change="onPageChange"></u-pagination>
	</div>
</template>

<script>
import AgentProfile from "./AgentProfile"
import AgentManagementHeader from "./AgentManagementHeader"
import UPagination from "../../visual/components/pagination/UPagination"
import moment from "moment"

export default {
  components: {
    AgentProfile,
    AgentManagementHeader,
    UPagination
  },
  props: ["agentManagement", "locale", "properties"],
  data() {
    return {
      defaultPageSize: 12,
      pagination: {},
      filter: "",
      sort: "",
      filteredData: [],
      pagedData: []
    }
  },
  computed: {
    title() {
      if (this.properties && this.properties.title) {
        return this.properties.title
      }
      return this.locale[this.agentManagement.tableData.title]
    }
  },
  mounted() {
    this.updateData()
  },
  watch: {
    agentManagement() {
      this.updateData()
    },
    filter() {
      this.updateData()
    },
    sort() {
      this.updateData()
    }
  },
  methods: {
    updateData: function() {
      this.filterAndSortData()
      this.paginateData()
    },
    filterAndSortData: function() {
      let agents = this.agentManagement.tableData.datasource
      if (agents) {
        agents = this.filterByName(agents)
        agents = this.sortData(agents)

        this.filteredData = agents
      } else {
        this.filteredData = []
      }
    },
    paginateData: function() {
      this.pagedData = this.filteredData.slice(this.pagination.start, this.pagination.end)
    },
    onPageChange: function(page) {
      this.pagination = page
      this.updateData()
    },
    onFilterChange: function(filter) {
      this.filter = filter
    },
    onSortChange: function(sort) {
      this.sort = sort
    },
    filterByName: function(fagents) {
      let self = this
      return fagents.filter(function(it) {
        if (self.filter) {
          let agentName = it.age_name.toLowerCase()
          return agentName.includes(self.filter.toLowerCase())
        }
        return true
      })
    },
    sortData: function(agents) {
      let self = this
      let taskSort = function(a, b) {
        return a.alltasks - b.alltasks
      }

      let agentNameSort = function(a, b) {
        if (a.age_name.toLowerCase() > b.age_name.toLowerCase()) return 1
        else return -1
      }

      let lastSyncSort = function(a, b) {
        let first = moment(a.lastsync_complete, "DD/MM/YY HH:mm")
        let second = moment(b.lastsync_complete, "DD/MM/YY HH:mm")

        if (!first.isValid() || !second.isValid()) {
          if (a.lastsync_complete < b.lastsync_complete) {
            return -1
          }
          if (a.lastsync_complete > b.lastsync_complete) {
            return 1
          }
        }
        if (first < second) {
          return -1
        }
        if (first > second) {
          return 1
        }
      }
      return agents.sort((a, b) => {
        if (self.sort === "task-asc") {
          return taskSort(a, b)
        }
        if (self.sort === "task-desc") {
          return taskSort(b, a)
        }

        if (self.sort === "sync-asc") {
          return lastSyncSort(a, b)
        }
        if (self.sort === "sync-desc") {
          return lastSyncSort(b, a)
        }

        //default sorting
        return agentNameSort(a, b)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.agent-profile-container {
  width: 100%;
  .box-cards {
    margin-top: 30px;
  }
  .pagination-container {
    position: absolute;
    bottom: 30px;
  }
}
</style>



