import { HorizontalBar, mixins } from "vue-chartjs"
const { reactiveProp } = mixins
import CustomLegendPlugin from "../CustomLegendPlugin"
import FullStackedBarChartPercentPlugin from "./FullStackedBarChartPercentPlugin"

export default {
  name: "FullStackedBarChartWrapper",
  extends: HorizontalBar,
  mixins: [reactiveProp],
  props: ["showLegend"],
  mounted() {
    Chart.plugins.register(CustomLegendPlugin)
    this.addPlugin(FullStackedBarChartPercentPlugin)
    this.renderChart(this.chartData, this.options)
  },
  data() {
    return {
      options: {
        responsive: true,
        maintainAspectRatio: false,
        fullStacked: true,
        plugins: {
          legend: false
        },
        legend: {
          display: this.showLegend,
          position: "bottom",
          labels: {
            onePerLine: true,
            fontFamily: "'Roboto'",
            fontSize: 13,
            boxWidth: 30
          }
        },
        tooltips: {
          enabled: true,
          mode: "point",
          callbacks: {
            label: function(itens, data) {
              return data.datasets[itens.datasetIndex].label + ": " + itens.xLabel.toFixed() + "%"
            }
          }
        },
        scales: {
          xAxes: [
            {
              maxBarThickness: 30,
              stacked: true,
              ticks: {
                min: 0,
                max: 100,
                callback: function(value) {
                  return value + "%"
                }
              },
              gridLines: {
                display: true,
                beginAtZero: true,
                drawBorder: false
              }
            }
          ],
          yAxes: [
            {
              maxBarThickness: 30,
              stacked: true
            }
          ]
        }
      }
    }
  }
}



