<template>
	<li class="l-select__list-item" :class="{'active' : option.active}" @click="click">
		<a class="dropdown-item l-dropdown__item">{{option.title}}</a>
	</li>
</template>

<script>
export default {
  props: {
    option: {}
  },
  methods: {
    click() {
      this.$emit("toggle", this.option)
    }
  }
}
</script>

<style lang="scss" scoped>
.l-select__list-item {
  &.active {
    color: inherit;
    background-color: #f8f9fa;
  }
}
.l-dropdown__item {
  font-size: 0.9em;
  &:active {
    color: inherit;
    background-color: #f8f9fa;
  }
}
</style>


