<template>
	<div class="agent-management-header">
		<u-title-label>
			<template slot="left">
				<span>{{title}}</span>
			</template>
		</u-title-label>
		<div class="row align-items-center">
			<div class="col-12 col-sm-6 col-md-8 col-lg-9 col-xl-9">
				<div class="row">
					<div class="col-12 col-sm-4 col-md-3">
						<u-select-dropdown :title="taskFilterLabel" :options="taskOptions" @changed="taskSort"></u-select-dropdown>
					</div>
					<div class="col-12 col-sm-6 col-md-3">
						<u-select-dropdown :title="syncFilterLabel" :options="syncOptions" @changed="syncSort"></u-select-dropdown>
					</div>
				</div>
			</div>
			<div class="col-12 col-sm-6 col-md-4 col-lg-3 col-xl-3">
				<div class="input-group">
					<input type="text" class="l-input form-control form-control-sm" :placeholder="searchLabel" @keyup="filterName">
					<div class="input-group-append">
						<span class="input-group-text">
							<i class="fas fa-search fa-xs"></i>
						</span>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import UTitleLabel from "../../visual/layout/label/UTitleLabel"
import USelectDropdown from "../../visual/components/dropdown/USelectDropdown"

export default {
  components: {
    UTitleLabel,
    USelectDropdown
  },
  props: ["title", "locale"],
  data() {
    return {
      taskOptions: [
        {
          title: this.locale.translate("dashboard.widget.agent.moreless"),
          active: false,
          value: "task-desc"
        },
        {
          title: this.locale.translate("dashboard.widget.agent.lessmore"),
          active: false,
          value: "task-asc"
        }
      ],
      syncOptions: [
        {
          title: this.locale.translate("dashboard.widget.agent.most-recent"),
          active: false,
          value: "sync-desc"
        },
        {
          title: this.locale.translate("dashboard.widget.agent.less-recent"),
          active: false,
          value: "sync-asc"
        }
      ]
    }
  },
  methods: {
    filterName(event) {
      this.$emit("filterchange", event.target.value)
    },
    taskSort(option) {
      this.inactiveAllSync()
      this.$emit("sortchange", option.value)
    },
    syncSort(option) {
      this.inactiveAllTask()
      this.$emit("sortchange", option.value)
    },
    inactiveAllTask() {
      for (let index in this.taskOptions) {
        this.taskOptions[index].active = false
      }
    },
    inactiveAllSync() {
      for (let index in this.syncOptions) {
        this.syncOptions[index].active = false
      }
    }
  },
  computed: {
    taskFilterLabel: function() {
      return this.locale.translate("dashboard.widget.agent.task")
    },
    syncFilterLabel: function() {
      return this.locale.translate("dashboard.widget.agent.sync")
    },
    searchLabel: function() {
      return this.locale.translate("common.search")
    }
  }
}
</script>

<style lang="scss" scoped>
.input-group {
  height: 26px;
  .l-input {
    box-shadow: none;
    font-size: inherit;
  }
}
.agent-management-header {
  padding-bottom: 15px;
  @media screen and (max-width: 575px) {
    padding-bottom: 30px;
  }
}
</style>



