import { Pie, mixins } from "vue-chartjs"
const { reactiveProp } = mixins
import CustomLegendPlugin from "../CustomLegendPlugin"
import PieChartPercentPlugin from "./PieChartPercentPlugin"

export default {
  extends: Pie,
  name: "PieChartWrapper",
  mixins: [reactiveProp],
  mounted() {
    Chart.plugins.register(CustomLegendPlugin)
    this.addPlugin(PieChartPercentPlugin)
    this.renderChart(this.chartData, this.options)
  },
  methods: {
    representativeShare: function(value, data) {
      let allData = data.datasets[0].data.reduce((acc, ds) => acc + ds, 0)
      let representative = ((value * 100) / (allData != 0 ? allData : 1)).toFixed()
      return `${representative}%`
    }
  },
  data() {
    return {
      options: {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          legend: false
        },
        tooltips: {
          callbacks: {
            label: (tooltipItem, data) => {
              let index = tooltipItem.index
              let value = data.datasets[0].data[index]
              return `${data.labels[index]}: ${value} (${this.representativeShare(value, data)})`
            }
          }
        },
        legend: {
          display: true,
          position: "bottom",
          labels: {
            onePerLine: true,
            fontFamily: "'Roboto'",
            fontSize: 13,

            boxWidth: 30,
            generateLabels: chart => {
              var data = chart.data

              chart.legend.afterFit = function() {
                var width = this.width
                this.lineWidths = this.lineWidths.map(() => this.width - 12)
              }

              if (data.labels.length && data.datasets.length) {
                return data.labels.map((label, i) => {
                  var meta = chart.getDatasetMeta(0)
                  var ds = data.datasets[0]
                  var arc = meta.data[i]
                  var custom = (arc && arc.custom) || {}
                  var getValueAtIndexOrDefault = Chart.helpers.getValueAtIndexOrDefault
                  var arcOpts = chart.options.elements.arc
                  var fill = custom.backgroundColor
                    ? custom.backgroundColor
                    : getValueAtIndexOrDefault(ds.backgroundColor, i, arcOpts.backgroundColor)
                  var stroke = custom.borderColor
                    ? custom.borderColor
                    : getValueAtIndexOrDefault(ds.borderColor, i, arcOpts.borderColor)
                  var bw = custom.borderWidth
                    ? custom.borderWidth
                    : getValueAtIndexOrDefault(ds.borderWidth, i, arcOpts.borderWidth)

                  var value = chart.config.data.datasets[arc._datasetIndex].data[arc._index]

                  return {
                    text: `${label}: ${value} (${this.representativeShare(value, data)})`,
                    fillStyle: fill,
                    strokeStyle: stroke,
                    lineWidth: bw,
                    hidden: isNaN(ds.data[i]) || meta.data[i].hidden,
                    index: i
                  }
                })
              } else {
                return []
              }
            }
          }
        }
      }
    }
  }
}



