<template>
  <div class="bar-chart-container">
    <bar-chart-wrapper class="bar-chart" :chart-data="chartData" :showLegend="chart.chartData.showLegend" v-if="!noDataAvailable"></bar-chart-wrapper>
    <no-data :show="noDataAvailable" :text="noDataLabel"></no-data>
    <u-pagination :page-size="defaultPageSize" :total="barLabel.length" @change="onPageChange"></u-pagination>
  </div>
</template>

<script>
import BarChartWrapper from "./BarChartWrapper"
import UPagination from "../../visual/components/pagination/UPagination"
import ChartHelper from "../../../helpers/ChartHelper"
import NoData from "../NoData"

export default {
  name: "BarChart",
  components: {
    BarChartWrapper,
    UPagination,
    NoData
  },
  props: {
    chart: {},
    locale: {},
    colors: {},
    noDataText: {
      default: "chart.no.data"
    }
  },
  data() {
    return {
      chartData: {},
      defaultPageSize: 5,
      pagination: {}
    }
  },
  watch: {
    chart() {
      this.updateData()
    }
  },
  mounted() {
    this.updateData()
  },
  methods: {
    updateData() {
      if (this.chart.data) this.chart.data.sort((a, b) => a.group_1.localeCompare(b.group_1))
      this.chartData = this.generateData()
    },
    generateData() {
      return {
        labels: this.pagedBarLabed,
        datasets: this.barDataSets
      }
    },
    barValues: function(metricIndex) {
      if (this.chart && this.chart.data) {
        return this.chart.data
          .map(it => {
            return { x: this.locale.translate(it.group_1), y: it["metric_" + metricIndex] }
          })
          .slice(this.pagination.start, this.pagination.end)
      }
      return []
    },
    onPageChange: function(page) {
      this.pagination = page
      this.updateData()
    }
  },
  computed: {
    noDataLabel() {
      return this.locale[this.noDataText]
    },
    noDataAvailable() {
      return ChartHelper.isEmptyChartData(this.chart)
    },
    barLabel: function() {
      if (this.chart && this.chart.data) {
        return this.chart.data.map(it => {
          let lbl = this.locale.translate(it.group_1)
          if (lbl.length > 13) lbl = lbl.substring(0, 13) + "..."
          return lbl
        })
      }
      return []
    },
    pagedBarLabed: function() {
      return this.barLabel.slice(this.pagination.start, this.pagination.end)
    },
    barDataSets: function() {
      if (!this.chart || !this.chart.metrics) return []

      return this.chart.metrics.map(m => {
        let index = this.chart.metrics.indexOf(m)
        return {
          label: this.locale.translate(m.description),
          backgroundColor: this.colors[index],
          borderWidth: 1,
          data: this.barValues(m.id),
          tooltip: this.locale.translate(m.description)
        }
      })
    }
  }
}
</script>
<style>
.bar-chart-container {
  height: 100%;
}

.bar-chart {
  height: calc(100% - 30px);
}
</style>



