import { Line, mixins } from "vue-chartjs"
const { reactiveProp } = mixins

export default {
  name: "LineChartWrapper",
  extends: Line,
  mixins: [reactiveProp],
  mounted() {
    this.renderChart(this.chartData, this.options)
  },
  data() {
    return {
      options: {
        maintainAspectRatio: false,
        legend: {
          display: this.chartData.showLegend
        },
        scales: {
          yAxes: [
            {
              gridLines: {
                display: true,
                drawBorder: false
              }
            }
          ]
        },
        tooltips: {
          enabled: true,
          mode: "point",
          callbacks: {
            label: function(itens, data) {
              return `${data.datasets[itens.datasetIndex].seriesName[itens.datasetIndex]}: ${itens.yLabel}`
            }
          }
        }
      }
    }
  }
}



