<template>
	<span class="c-icon-container" :class="{'disabled' : disabled}" :title="tooltip">
		<i :class="faIcon"></i>
	</span>
</template>

<script>
export default {
  name: "UIcon",
  props: {
    faIcon: {},
    tooltip: {},
    disabled: {
      type: Boolean
    }
  }
}
</script>

<style lang="scss" scoped>
.c-icon-container {
  color: #999;
  &.icon-danger {
    color: lightcoral;
  }
  &.disabled {
    opacity: 0.3;
    cursor: not-allowed;
  }
}

a:hover {
  .c-icon-container {
    color: #666;
    &.icon-danger {
      color: red;
    }
  }
}
</style>



