<template>
	<div>
		<u-label :text="label" v-if="label"></u-label>
		<div>
			<el-select v-model="selectedValue" :disabled="disabled" :clearable="clearable" :multiple="multiple" collapse-tags :filterable="filterable" :placeholder="placeholder" :no-data-text="noDataText" @change="change">
				<el-option v-for="option in options" :key="option.value" :label="option.label" :value="option.value" />
			</el-select>
		</div>
	</div>
</template>

<script>
import ULabel from "../../layout/label/ULabel"

export default {
  name: "USelect",
  components: {
    ULabel
  },
  props: {
    label: {
      type: String
    },
    placeholder: {
      type: String,
      default: ""
    },
    filterable: {
      type: Boolean,
      default: true
    },
    multiple: {
      type: Boolean,
      default: false
    },
    clearable: {
      type: Boolean,
      default: true
    },
    disabled: {
      type: Boolean,
      default: false
    },
    required: {
      type: Boolean,
      default: false
    },
    autoSelect: {
      type: Boolean,
      default: false
    },
    noDataText: {
      type: String,
      default: ""
    },
    options: {
      type: Array
    },
    value: {}
  },
  data() {
    return {
      selectedValue: this.value
    }
  },
  watch: {
    value() {
      this.selectedValue = this.value
    }
  },
  methods: {
    change(val) {
      this.$emit("input", val)
      this.$emit("change", val)
    }
  }
}
</script>

<style lang="scss">
.el-select {
  width: 100%;

  .el-select-dropdown__list {
    padding: 0px 8px;
  }

  .el-input__inner {
    padding-top: 3px;
    padding-bottom: 3px;
  }

  .el-select__input {
    padding-left: 5px;
    margin-left: 0px;
    max-width: 30px !important;
  }

  .el-input__inner,
  .el-select-dropdown__item {
    font-size: 12px;
    height: initial !important;
    line-height: 20px;
  }
  .el-select__tags {
    width: calc(100% - 35px);
    flex-wrap: nowrap;

    & > span {
      width: 100%;
    }

    .el-select__tags-text {
      font-size: 0.9em;
      width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    .el-tag--small {
      height: 17px;
      display: inline-flex;
      align-items: center;

      &:first-child {
        max-width: calc(100% - 50px);
      }

      .el-tag__close {
        top: 1px;
      }
    }
  }
}
</style>





